'use client'
import React, { useEffect, useRef, useState } from 'react'
import Peer from 'simple-peer'
import { getSocket } from '@/lib/socket'
import { Mic, MicOff, PhoneOff, Phone, Volume2 } from 'lucide-react'
import { motion } from 'motion/react'

interface VoiceCallProps {
    roomId: string
    userId: string      // "Me"
    otherUserId: string // "Them"
    isInitiator: boolean
    incomingSignal?: any
    onEnd: () => void
}

export default function VoiceCall({ roomId, userId, otherUserId, isInitiator, incomingSignal, onEnd }: VoiceCallProps) {
    const [stream, setStream] = useState<MediaStream | null>(null)
    const [callAccepted, setCallAccepted] = useState(false)
    const [isMuted, setIsMuted] = useState(false)
    const [status, setStatus] = useState(isInitiator ? "Calling..." : "Connecting...")

    const myVideo = useRef<HTMLVideoElement>(null)
    const userVideo = useRef<HTMLVideoElement>(null)
    const connectionRef = useRef<Peer.Instance | null>(null)
    const socket = getSocket()

    useEffect(() => {
        navigator.mediaDevices.getUserMedia({ video: false, audio: true })
            .then((currentStream) => {
                setStream(currentStream)
                if (myVideo.current) {
                    myVideo.current.srcObject = currentStream
                }

                // Initialize Peer
                const peer = new Peer({
                    initiator: isInitiator,
                    trickle: false,
                    stream: currentStream
                })

                peer.on('signal', (data: any) => {
                    if (isInitiator) {
                        socket.emit('call-user', { userToCall: otherUserId, signalData: data, from: userId, roomId })
                    } else {
                        socket.emit('answer-call', { signal: data, to: otherUserId, from: userId, roomId })
                    }
                })

                peer.on('stream', (remoteStream: MediaStream) => {
                    if (userVideo.current) {
                        userVideo.current.srcObject = remoteStream
                    }
                })

                peer.on('connect', () => {
                    setStatus("Connected")
                    setCallAccepted(true)
                })

                peer.on('close', () => {
                    cleanup()
                })

                peer.on('error', (err: any) => {
                    console.error("Peer error:", err)
                    setStatus("Connection Failed")
                })

                connectionRef.current = peer

                // Socket Listeners for Signals
                if (isInitiator) {
                    socket.on('call-accepted', (signal: any) => {
                        setCallAccepted(true)
                        peer.signal(signal)
                    })
                } else {
                    if (incomingSignal) {
                        peer.signal(incomingSignal)
                    }
                }

            })
            .catch((err) => {
                console.error("Media Error:", err)
                setStatus("Microphone Access Denied")
            })

        return () => {
            cleanup()
        }
    }, [])

    const cleanup = () => {
        connectionRef.current?.destroy()
        socket.off('call-accepted')
        stream?.getTracks().forEach(track => track.stop())
        onEnd()
    }

    const toggleMute = () => {
        if (stream) {
            stream.getAudioTracks()[0].enabled = !stream.getAudioTracks()[0].enabled
            setIsMuted(!isMuted)
        }
    }

    return (
        <div className="flex flex-col items-center justify-center p-8 bg-zinc-900 text-white rounded-[2rem] h-full w-full relative overflow-hidden">
            {/* Ambient Background */}
            <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-64 h-64 bg-green-500/20 rounded-full blur-[100px] animate-pulse" />

            <div className="relative z-10 text-center space-y-8">
                <div className="relative">
                    <div className="w-32 h-32 bg-zinc-800 rounded-full flex items-center justify-center mx-auto border-4 border-zinc-700 shadow-2xl">
                        <Phone size={48} className="text-zinc-500" />
                    </div>
                    {callAccepted && (
                        <div className="absolute -bottom-2 -right-2 bg-green-500 p-3 rounded-full border-4 border-zinc-900">
                            <Volume2 size={20} className="animate-pulse" />
                        </div>
                    )}
                </div>

                <div>
                    <h3 className="text-2xl font-black tracking-tight mb-2">
                        {status === "Connected" ? "Audio Call Active" : status}
                    </h3>
                    <p className="text-zinc-400 text-sm font-bold uppercase tracking-widest">{isInitiator ? "Calling Buyer..." : "Incoming Call..."}</p>
                </div>

                {/* Hidden Video Elements for Audio Stream */}
                <video playsInline muted ref={myVideo} autoPlay className="hidden" />
                <video playsInline ref={userVideo} autoPlay className="hidden" />

                <div className="flex items-center gap-6 justify-center pt-8">
                    <button
                        onClick={toggleMute}
                        className={`p-6 rounded-full transition-all ${isMuted ? 'bg-white text-zinc-900' : 'bg-zinc-800 text-white hover:bg-zinc-700'}`}
                    >
                        {isMuted ? <MicOff size={24} /> : <Mic size={24} />}
                    </button>

                    <button
                        onClick={cleanup}
                        className="p-6 bg-red-500 text-white rounded-full hover:bg-red-600 transition-all shadow-lg shadow-red-500/20 scale-110"
                    >
                        <PhoneOff size={28} />
                    </button>
                </div>
            </div>
        </div>
    )
}
